// Global submit postcod styles
// ==================================
.post-code-submit {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  width: 100%;
  max-width: cre(539px);

  p {
    margin: 0;
  }

  &.white {

    input[type='text'] {
      background-color: white;
    }
  }

  input[type='text'] {
    background-color: #f1f1f1;
    border-radius: 8px 0 0 8px;
    border: 0;
    width: 453px;
    height: 100%;
    max-height: cre(86px);
    text-align: center;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
  }

  input[type='submit'] {
    @include button-reset;
    font-size: 0;
    background-color: transparent;
    color: transparent;
    width: cre(86px);
    height: cre(86px);
    position: relative;
  }

  .submit-button {
    background-color: $m2;
    border-radius: 0 8px 8px 0;
    position: relative;
    max-width: 86px;
    max-height: cre(86px);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);

    &::before {
      @include font-icon('\e90c');
      font-size: cre(20px);
      color: white;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

// Hero Specific form
// ==================================
.hero-form-container {
  position: absolute;
  bottom: cre(70px);
  left: cre(80x);

  .contact-us & {
    display: none;
  }

  h3 {
    color: white;
    margin-bottom: cre(20px);
    text-shadow: 0 -1px 8px rgba(0,0,0,0.5);
  }
}

#hero-form {

  input[type='text'] {
    width: cre(374px);
  }

  input[type='submit'] {
    font-size: cre(14px);
    padding-left: cre(5px);
    padding-right: cre(5px);
    width: cre(71px);
    height: cre(71px);
  }

  .submit-button {
    max-width: cre(71px);
    max-height: cre(71px);
  }
}

// Contact form
// ==================================
.contact-form {
  width: 100%;
  max-width: cre(700px);
  margin-left: auto;
  margin-right: auto;

  label {
    font-weight: bold;
    color: $m3;
    width: 100%;
  }

  input[type='text'],
  input[type='email'],
  input[type='tel'] {
    padding: cre(10px);
    width: 100%;
  }

  textarea {
    width: 100%;
  }

  .name-fields {
    display: flex;
    flex-flow: row;
    justify-content: space-between;

    label {
      margin-right: cre(30px);

      &:last-child {
        margin-right: 0;
      }
    }

    span {
      display: block;
    }
  } // .name-fields

  input[type='submit'] {
    @include button-reset;
    font-size: cre(21px);
    font-weight: 600;
    line-height: cre(35px);
    background-color: $m2;
    border-radius: 11px;
    color: white;
    padding: cre(10px) cre(30px);
    text-transform: uppercase;
    transition: 0.3s;

    &:focus,
    &:hover {
      background-color: darken($m2, 10%);
    }
  }

  .button-container {
    text-align: right;

    .ajax-loader {
      display: none;
    }
  }
} // .contact-form